<template>
  <div class="z-drr-container" 
    @mousedown.stop="elmDown" 
    :style="style"
    :class="{ 
      'z-draggable': draggable, 
      'z-resizable': resizable, 
      'z-rotatable': rotatable, 
      'z-active': enabled,
      'z-dragging': dragging, 
      'z-resizing': resizing,
      'z-rotating': rotating
    }"
  >
    <slot></slot>
    
    <div
      v-if="rotatable"
      class="z-rotateable-handle"
      :style="{ display: enabled ? 'block' : 'none'}"
      @mousedown.stop.prevent="rotating = true"
    ></div>
    <div
      class="z-resizeable-handle"
      v-if="resizable"
      v-for="handle in handles"
      :class="'z-handle-' + handle"
      :style="{ display: enabled ? 'block' : 'none'}"
      @mousedown.stop.prevent="handleResizeStart(handle, $event)"
    ></div>
  </div>
</template>

<script>
export default {
  replace: true,
  name: 'vue-drr',
  props: {
    active: {
      type: Boolean, default: false
    },
    draggable: {
      type: Boolean, default: true
    },
    resizable: {
      type: Boolean, default: true
    },
    rotatable: {
      type: Boolean, default: true
    },
    w: {
      type: Number,
      default: 200,
      validator: function (val) {
        return val > 0
      }
    },
    h: {
      type: Number,
      default: 200,
      validator: function (val) {
        return val > 0
      }
    },
    minw: {
      type: Number,
      default: 50,
      validator: function (val) {
        return val > 0
      }
    },
    minh: {
      type: Number,
      default: 50,
      validator: function (val) {
        return val > 0
      }
    },
    angle: {
      type: Number,
      default: 0,
      validator: function (val) {
        return typeof val === 'number'
      }
    },
    x: {
      type: Number,
      default: 0,
      validator: function (val) {
        return typeof val === 'number'
      }
    },
    y: {
      type: Number,
      default: 0,
      validator: function (val) {
        return typeof val === 'number'
      }
    },
    z: {
      type: [ String, Number ],
      default: 'auto',
      validator: function (val) {
        let valid = (typeof val === 'string') ? val === 'auto' : val >= 0
        return valid
      }
    },
    handles: {
      type: Array,
      default: function () {
        return ['n', 'e', 's', 'w', 'nw', 'ne', 'se', 'sw']
      }
    },
    axis: {
      type: String,
      default: 'both',
      validator: function (val) {
        return ['x', 'y', 'both'].indexOf(val) !== -1
      }
    },
    grid: {
      type: Array,
      default: function () {
        return [1, 1]
      }
    },
    parent: {
      type: Boolean, default: false
    }
  },
  created: function () {
    this.parentX = 0
    this.parentW = 9999
    this.parentY = 0
    this.parentH = 9999

    this.mouseX = 0
    this.mouseY = 0

    this.lastMouseX = 0
    this.lastMouseY = 0

    this.mouseOffX = 0
    this.mouseOffY = 0

    this.elmX = 0
    this.elmY = 0

    this.elmW = 0
    this.elmH = 0
  },
  mounted: function () {
    document.documentElement.addEventListener('mousemove', this.handleMove, true)
    document.documentElement.addEventListener('mousedown', this.deselect, true)
    document.documentElement.addEventListener('mouseup', this.handleUp, true)

    this.elmX = parseInt(this.$el.style.left)
    this.elmY = parseInt(this.$el.style.top)
    this.elmW = this.$el.offsetWidth || this.$el.clientWidth
    this.elmH = this.$el.offsetHeight || this.$el.clientHeight

    this.reviewDimensions()
  },
  beforeDestroy: function () {
    document.documentElement.removeEventListener('mousemove', this.handleMove, true)
    document.documentElement.removeEventListener('mousedown', this.deselect, true)
    document.documentElement.removeEventListener('mouseup', this.handleUp, true)
  },
  data: function () {
    return {
      top: this.y,
      left: this.x,
      width: this.w,
      height: this.h,
      rotateAngle: this.angle,
      resizing: false,
      dragging: false,
      rotating: false,
      enabled: this.active,
      handle: null,
      zIndex: this.z
    }
  },
  methods: {
    reviewDimensions: function () {
      if (this.minw > this.w) this.width = this.minw

      if (this.minh > this.h) this.height = this.minh

      if (this.parent) {
        const parentW = parseInt(this.$el.parentNode.clientWidth, 10)
        const parentH = parseInt(this.$el.parentNode.clientHeight, 10)

        this.parentW = parentW
        this.parentH = parentH

        if (this.w > this.parentW) this.width = parentW

        if (this.h > this.parentH) this.height = parentH

        if ((this.x + this.w) > this.parentW) this.width = parentW - this.x

        if ((this.y + this.h) > this.parentH) this.height = parentH - this.y

        this.elmW = this.width
        this.elmH = this.height
      }

      this.$emit('resizing', this.left, this.top, this.width, this.height)
    },
    elmDown: function (e) {
      const target = e.target || e.srcElement

      if (this.$el.contains(target)) {
        this.reviewDimensions()

        if (!this.enabled) {
          this.enabled = true

          this.$emit('activated')
          this.$emit('update:active', true)
        }

        if (this.draggable) {
          this.dragging = true
        }
      }
    },
    deselect: function (e) {
      e.preventDefault()  // 防止拖动时文字被选中
      this.mouseX = e.pageX || e.clientX + document.documentElement.scrollLeft
      this.mouseY = e.pageY || e.clientY + document.documentElement.scrollTop

      this.lastMouseX = this.mouseX
      this.lastMouseY = this.mouseY

      const target = e.target || e.srcElement
      const regex = new RegExp('z-handle-([trmbl]{2})', '')

      if (!this.$el.contains(target) && !regex.test(target.className)) {
        if (this.enabled) {
          this.enabled = false

          this.$emit('deactivated')
          this.$emit('update:active', false)
        }
      }
    },
    handleResizeStart: function (handle, e) {
      this.handle = handle

      if (e.stopPropagation) e.stopPropagation()
      if (e.preventDefault) e.preventDefault()

      this.resizing = true
    },
    handleMove: function (e) {
      const startX = this.lastMouseX
      const startY = this.lastMouseY

      this.mouseX = e.pageX || e.clientX + document.documentElement.scrollLeft
      this.mouseY = e.pageY || e.clientY + document.documentElement.scrollTop

      let diffX = this.mouseX - this.lastMouseX + this.mouseOffX
      let diffY = this.mouseY - this.lastMouseY + this.mouseOffY

      this.mouseOffX = this.mouseOffY = 0

      this.lastMouseX = this.mouseX
      this.lastMouseY = this.mouseY

      let dX = diffX
      let dY = diffY

      if (this.resizing) {
        if (this.handle.indexOf('n') >= 0) {
          if (this.elmH - dY < this.minh) this.mouseOffY = (dY - (diffY = this.elmH - this.minh))
          else if (this.elmY + dY < this.parentY) this.mouseOffY = (dY - (diffY = this.parentY - this.elmY))
          this.elmY += diffY
          this.elmH -= diffY
        }

        if (this.handle.indexOf('s') >= 0) {
          if (this.elmH + dY < this.minh) this.mouseOffY = (dY - (diffY = this.minh - this.elmH))
          else if (this.elmY + this.elmH + dY > this.parentH) this.mouseOffY = (dY - (diffY = this.parentH - this.elmY - this.elmH))
          this.elmH += diffY
        }

        if (this.handle.indexOf('w') >= 0) {
          if (this.elmW - dX < this.minw) this.mouseOffX = (dX - (diffX = this.elmW - this.minw))
          else if (this.elmX + dX < this.parentX) this.mouseOffX = (dX - (diffX = this.parentX - this.elmX))
          this.elmX += diffX
          this.elmW -= diffX
        }

        if (this.handle.indexOf('e') >= 0) {
          if (this.elmW + dX < this.minw) this.mouseOffX = (dX - (diffX = this.minw - this.elmW))
          else if (this.elmX + this.elmW + dX > this.parentW) this.mouseOffX = (dX - (diffX = this.parentW - this.elmX - this.elmW))
          this.elmW += diffX
        }

        this.left = (Math.round(this.elmX / this.grid[0]) * this.grid[0])
        this.top = (Math.round(this.elmY / this.grid[1]) * this.grid[1])

        this.width = (Math.round(this.elmW / this.grid[0]) * this.grid[0])
        this.height = (Math.round(this.elmH / this.grid[1]) * this.grid[1])

        this.$emit('resizing', this.left, this.top, this.width, this.height)
      } else if (this.dragging) {
        if (this.parent) {
          if (this.elmX + dX < this.parentX) this.mouseOffX = (dX - (diffX = this.parentX - this.elmX))
          else if (this.elmX + this.elmW + dX > this.parentW) this.mouseOffX = (dX - (diffX = this.parentW - this.elmX - this.elmW))

          if (this.elmY + dY < this.parentY) this.mouseOffY = (dY - (diffY = this.parentY - this.elmY))
          else if (this.elmY + this.elmH + dY > this.parentH) this.mouseOffY = (dY - (diffY = this.parentH - this.elmY - this.elmH))
        }

        this.elmX += diffX
        this.elmY += diffY

        if (this.axis === 'x' || this.axis === 'both') {
          this.left = (Math.round(this.elmX / this.grid[0]) * this.grid[0])
        }
        if (this.axis === 'y' || this.axis === 'both') {
          this.top = (Math.round(this.elmY / this.grid[1]) * this.grid[1])
        }

        this.$emit('dragging', this.left, this.top)
      } else if (this.rotating) {
        const origin = this.origin
        const startAngle = Math.atan2(startY - origin.y, startX - origin.x)
        const endAngle = Math.atan2(this.lastMouseY - origin.y, this.lastMouseX - origin.x)
        this.rotateAngle += (endAngle - startAngle) * 180 / Math.PI
        this.$emit('rotating', this.rotateAngle)
      }
    },
    handleUp: function (e) {
      this.handle = null
      if (this.resizing) {
        this.resizing = false
        this.$emit('resizestop', this.left, this.top, this.width, this.height)
      }
      if (this.dragging) {
        this.dragging = false
        this.$emit('dragstop', this.left, this.top)
      }

      if (this.rotating) {
        this.rotating = false
        this.$emit('rotatestop', this.rotateAngle)
      }

      this.elmX = this.left
      this.elmY = this.top
    }
  },
  computed: {
    origin: function () {
      return {
        x: this.left + this.width / 2,
        y: this.top + this.width / 2
      }
    },
    style: function () {
      return {
        top: this.top + 'px',
        left: this.left + 'px',
        width: this.width + 'px',
        height: this.height + 'px',
        zIndex: this.zIndex,
        transform: 'rotate(' + this.rotateAngle + 'deg)'
      }
    }
  },
  watch: {
    active: function (val) {
      this.enabled = val
    },
    z: function (val) {
      if (val >= 0 || val === 'auto') {
        this.zIndex = val
      }
    }
  }
}
</script>

<style lang="scss">
  $main-color: #32a6d0;
  $circle-size: 8px;

  .z-drr-container {
    position: absolute;
  }

  .z-draggable:hover {
    cursor: move;
  }

  @mixin circle($bgc: #fff) {
    position: absolute;
    width: $circle-size;
    height: $circle-size;
    border: 1px solid $main-color;
    border-radius: 50%;
    background: $bgc;
  }

  .z-rotateable-handle {
    position: absolute;
    left: 50%;
    top: -$circle-size * 3;
    width: 1px;
    height: $circle-size * 3;
    // margin-left: -.5px;
    background-color: $main-color;
    cursor: url(../assets/mouserotate.png) 16 16, default;
    &:after {
      content: ' ';
      top: 0;
      left: 0;
      margin-left: -$circle-size/2;
      @include circle($main-color);
    }
  }

  .z-resizeable-handle {
    display: none;
    position: absolute;
  }

  .z-handle-nw,
  .z-handle-ne,
  .z-handle-sw,
  .z-handle-se {
    @include circle;
  }

  .z-handle-nw {
    top: -$circle-size/2;
    left: -$circle-size/2;
    cursor: nw-resize;
  }

  .z-handle-ne {
    top: -$circle-size/2;
    right: -$circle-size/2;
    cursor: ne-resize;
  }

  .z-handle-sw {
    bottom: -$circle-size/2;
    left: -$circle-size/2;
    cursor: sw-resize;
  }

  .z-handle-se {
    bottom: -$circle-size/2;
    right: -$circle-size/2;
    cursor: se-resize;
  }

  .z-handle-n,
  .z-handle-w,
  .z-handle-e,
  .z-handle-s {
    &:after {
      content: ' ';
      @include circle;
    }
  }

  .z-handle-n {
    top: 0;
    left: 0;
    width: 100%;
    height: $circle-size/2;
    border-top: 1px solid $main-color;
    cursor: n-resize;
    &:after {
      bottom: 0;
      left: 50%;
      margin-left: -$circle-size/2;
    }
  }

  .z-handle-w {
    top: 0;
    left: 0;
    width: $circle-size/2;
    height: 100%;
    border-left: 1px solid $main-color;
    cursor: w-resize;
    &:after {
      top: 50%;
      right: 0;
      margin-top: -$circle-size/2;
    }
  }

  .z-handle-e {
    top: 0;
    right: 0;
    width: $circle-size/2;
    height: 100%;
    border-right: 1px solid $main-color;
    cursor: e-resize;
    &:after {
      top: 50%;
      left: 0;
      margin-top: -$circle-size/2;
    }
  }

  .z-handle-s {
    bottom: 0;
    left: 0;
    width: 100%;
    height: $circle-size/2;
    border-bottom: 1px solid $main-color;
    cursor: s-resize;
    &:after {
      top: 0;
      left: 50%;
      margin-left: -$circle-size/2;
    }
  }
</style>



